section#navbar {
  top: 0;
  left: 0;
  width: 100%;
  position: fixed;
  background-color: #890025;
  z-index: 10;
  transition: all 0.4s ease;
  .container {
    padding-top: 35px;
    padding-bottom: 35px;
    transition: all 0.4s ease;

    a {
      color: white;
    }

    .logo * {
      font-family: "Vidaloka";
      font-size: 28px;
      font-weight: 700;
      letter-spacing: 1px;
    }

    .open-navbar {
      font-size: 20px !important;
    }

    .nav {
      .nav-item {
        display: inline-block;
        &:not(:last-child) {
          margin-right: 50px;
        }
      }
    }
  }

  &.scrolled {
    position: fixed;
    background-color: #74011f;
    // box-shadow: 0 0 15px 1px rgba($color: #000000, $alpha: .3);
    .container {
      padding-top: 25px;
      padding-bottom: 25px;
    }
  }

  @media (max-width: 992px) {
    background-color: #74011f;
    .container {
      overflow: hidden;
      padding-top: 25px;
      padding-bottom: 25px;
      .logo * {
        font-size: 20px;
        font-weight: 600;
      }
      .nav {
        height: 0;
        transition: height 0.4s ease;
        .nav-item {
          display: block;
          width: 100%;
          margin: 10px 0;
          &:first-child {
            margin-top: 20px;
          }
        }
        &.opened {
          height: 260px;
        }

        .nav-klik {
          height: 0;
          transition: height 0.4s ease;
          margin-top: 50px;
          transition: all 0.4s ease;
          .nav-item {
            display: block;
            width: 100%;
            margin-top: 10px 0;
            transition: all 0.4s ease;
          }
        }
        &.opened {
          height: 260px;
        }
      }
    }
  }
}

.audio-control,
.totop-btn {
  z-index: 9999;
  background-color: rgb(37, 37, 37);
  position: fixed;
  left: 15px;
  width: 50px;
  height: 50px;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: white;
  border-radius: 50%;
  transition: all 0.4s ease;
  display: flex;
}

.audio-control {
  bottom: 15px;
  opacity: 1;
  &:hover {
    opacity: 0.75;
    color: white;
  }
}

.totop-btn {
  bottom: 70px;
  opacity: 0;
  &.scrolled {
    opacity: 1;
  }
  &:hover {
    opacity: 0.75;
    color: white;
  }
}
