section#template-video {
  background: url("../../_assets/img/clipart/bg-2.jpg") no-repeat;
  background-size: cover;
  .container {
    padding-top: 80px;
    padding-bottom: 100px;

    h1,
    h1 * {
      font-family: "Vidaloka";
      font-size: 44px;
      color: #33000e;
      line-height: 60px;
      margin-bottom: 28px;
    }

    p {
      text-align: center;
      font-size: 18px;
    }

    .row:nth-child(2) {
      margin-top: 85px;
      margin-bottom: 70px;
    }

    .card {
      padding: 35px 30px;
      border-radius: 12px;
      border-color: transparent;
      box-shadow: 0px 6px 40px rgba(0, 0, 0, 0.1);
      display: flex;
      align-items: center;
      text-align: center;
      margin-bottom: 30px;
      h3 {
        font-family: "Vidaloka";
        font-size: 37px;
        margin-bottom: 34px;
      }
      .img-container {
        width: 100%;
        border-radius: 10px;
        overflow: hidden;
        margin-bottom: 50px;
        position: relative;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        .play-icon {
          width: 57px;
          height: 57px;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate3d(-50%, -50%, 0);
          z-index: 2;
          animation: play-anim 1.5s ease infinite !important;

          @keyframes play-anim {
            50% {
              width: 70px;
              height: 70px;
              opacity: 0.5;
            }
          }
        }
      }

      a.btn {
        width: 100%;
        padding: 20px;
        background-color: #890025;
        color: #fff;
        border-radius: 12px;
      }
      a.btn:hover {
        background-color: #be1341;
        color: #fff;
      }
    }

    a.btn-more {
      padding: 20px;
      width: 100%;
      background-color: transparent;
      border: 2px solid #890025;
      color: #890025;
      border-radius: 12px;
      font-weight: 500;
      &:hover {
        background-color: #890025;
        opacity: 1;
        color: #fff;
      }
    }
  }

  @media (max-width: 576px) {
    .container {
      padding-top: 50px !important;
      padding-bottom: 50px !important;
      h1,
      h1 * {
        font-size: 30px;
        line-height: 40px;
        text-align: center;
      }
      p {
        font-size: 16px;
      }

      .row:nth-child(2) {
        margin-top: 50px;
        margin-bottom: 30px;
      }

      .card {
        padding: 20px;
        margin-bottom: 25px;
        h3 {
          font-size: 30px;
          margin-bottom: 10px;
        }
        .img-container {
          margin-bottom: 30px;
        }
        a.btn {
          padding: 15px;
        }
      }
    }
  }
}
