section#closing {
  background: url("../../_assets/img/clipart/bg-closing.jpg") no-repeat;
  background-attachment: fixed;
  background-size: cover;
  position: relative;
  .background-filter {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(137, 0, 37, 0.33);
  }
  .container {
    padding-top: 120px;
    padding-bottom: 120px;
    position: relative;

    h1 {
      font-family: "Vidaloka";
      color: white;
      text-align: center;
      font-size: 44px;
      line-height: 60px;
      margin-bottom: 74px;
    }

    .btn {
      padding: 20px 36px;
      border-radius: 10px;
      font-size: 18px;
      border: 2px solid white;
      &.btn-1 {
        background-color: white;
        color: #890025;
        &.btn-1:hover {
          background-color: rgb(236, 228, 228, 0.8);
        }
      }

      &.btn-2 {
        color: white;
        &.btn-2:hover {
          background-color: #890025;
          color: #fff;
        }
      }
    }
  }

  @media (max-width: 576px) {
    .container {
      padding-top: 50px !important;
      padding-bottom: 50px !important;
      h1 {
        font-size: 20px;
        line-height: 30px;
        margin-bottom: 74px;
      }
      .btn {
        padding: 15px 25px;
        font-size: 14px;
      }
    }
  }
}
